import React, { useEffect, useContext, useState } from 'react';
import { Row, Col } from 'reactstrap';

import Card from '../../../shared/molecules/card';
import { getLicenses } from '../../../shared/services/license';
import ErrorContext from '../../../shared/modules/error/context';
import LicensePage from './licensePage';

const LicenseContainer = () => {
  const [state, setState] = useState({
    licenses: [],
    reset: 0,
    isLoading: false,
  });
  const { setError } = useContext(ErrorContext);

  useEffect(() => {
    async function fetchAllLicenses() {
      setState(s => ({ ...s, isLoading: true }));
      try {
        const licenses = await getLicenses();
        setState(s => ({ ...s, licenses, isLoading: false }));
      } catch (e) {
        setError(e, true);
        setState(s => ({ ...s, isLoading: false }));
      }
    }

    fetchAllLicenses();
  }, [setError]);

  return (
    <Row>
      <Col>
        <Card header="Licenses">
          {state.isLoading ? <span>Loading...</span> : <LicensePage licenses={state.licenses} />}
        </Card>
      </Col>
    </Row>
  );
};

export default LicenseContainer;
