import React, { useEffect, useContext, useState } from 'react';
import { Row, Col } from 'reactstrap';

import Card from '../../../shared/molecules/card';
import { getUsers } from '../../../shared/services/users';
import ErrorContext from '../../../shared/modules/error/context';
import UserPage from './userPage';

const CameraSetup = () => {
  const [state, setState] = useState({
    users: [],
    reset: 0,
    isLoading: false,
  });
  const { setError } = useContext(ErrorContext);

  useEffect(() => {
    async function fetchAllUsers() {
      setState(s => ({ ...s, isLoading: true }));
      try {
        const users = await getUsers();
        setState(s => ({ ...s, users, isLoading: false }));
      } catch (e) {
        setError(e, true);
        setState(s => ({ ...s, isLoading: false }));
      }
    }

    fetchAllUsers();
  }, [setError]);

  return (
    <Row>
      <Col>
        <Card header="Users">{state.isLoading ? <span>Loading...</span> : <UserPage users={state.users} />}</Card>
      </Col>
    </Row>
  );
};

export default CameraSetup;
