import React, { useEffect, useContext, useState } from 'react';
import { Row, Col } from 'reactstrap';

import Card from '../../../shared/molecules/card';
import { getOrganizations } from '../../../shared/services/organization';
import ErrorContext from '../../../shared/modules/error/context';
import OrgPage from './orgPage';

const LicenseContainer = () => {
  const [state, setState] = useState({
    orgs: [],
    reset: 0,
    isLoading: false,
  });
  const { setError } = useContext(ErrorContext);

  useEffect(() => {
    async function fetchAllLicenses() {
      setState(s => ({ ...s, isLoading: true }));
      try {
        const orgs = await getOrganizations();
        setState(s => ({ ...s, orgs, isLoading: false }));
      } catch (e) {
        setError(e, true);
        setState(s => ({ ...s, isLoading: false }));
      }
    }

    fetchAllLicenses();
  }, [setError]);

  return (
    <Row>
      <Col>
        <Card header="Organizations">{state.isLoading ? <span>Loading...</span> : <OrgPage orgs={state.orgs} />}</Card>
      </Col>
    </Row>
  );
};

export default LicenseContainer;
